.fse-table-of-content-wrapper {
	background-color: #FFCF00;
	border: 2px solid #FFCF00;
	width: 100%;
	border-radius: 8px;
}
.fse-table-inner-content{
	background-color: #ffffff;
	height: auto;
	width: 100%;
	border-radius: 8px;
}
.table-of-content-title {
	padding: 20px;
	text-align: center;
	font-size: 21px;
	font-family: 'Plus Jakarta Sans', sans-serif;
	font-weight: 800;
}
.fse-content-main-heading {
	font-size: 16px;
	font-family: 'Plus Jakarta Sans', sans-serif;
	font-weight: 700;
	text-decoration: underline;
	text-align: center;
	padding-top: 20px;
}
.fse-listing-content-wrapper {
	font-size: 14px;
	font-weight: 400;
	font-family: 'Plus Jakarta Sans', sans-serif;
	list-style: none;
}
.fse-list-content {
	margin-bottom: 20px;
}
.fse-list-item {
    margin-bottom: 10px;
    margin-top: 10px;
    list-style: none;
}
.fse-list-item-wrapper {
	border-left: 1px solid #d2d3d4;
   padding-left: 15px;
}
.fse-listing-bottom-content {
	padding-bottom: 20px;
}